video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.slideShow-container {
    /* background: url('/images/img-home.jpg') center center/cover no-repeat;   */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.slideShow-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: 100px;
}

.slideShow-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 
    'Lucida Sans', Arial, sans-serif;
}

.fa-play-circle{
    margin-left: 0.2rem;
}

.slideShow-btns {
    margin-top: 32px;
}

.slideShow-btns .btn{
    margin-left: 4px;
}

@media screen and (max-width:960px) {

    .slideShow-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width:768px){

    .slideShow-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    }
    
    .slideShow-container > p {
        font-size: 30px;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
    }

    .btn {
        width: 100%;
    }
}